<template>
  <div class="stats tank-list">
    <!--<h1>{{ tankList }}</h1>-->
    <div class="card" v-for="(list, index) in tankList" :key="list.id">
      <img class="card-image" :src="list.img"/>
      <div class="card-inner">
        <h3>{{ list.name }}</h3>
        <p>Width: {{ list.size.w }}</p>
        <p>Height: {{ list.size.h }}</p>
        <p>Depth: {{ list.size.d }}</p>
        <h4>Stock</h4>
        <ul>
          <li v-for="stock in list.stock" >{{ stock.name }} x {{ stock.amount }}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'tankList',
  data () {
    return {
      tankList: [
        {
          id: 1,
          name: 'Axolotl Tank',
          img: 'https://placehold.it/200x100',
          size: {
            w: 100,
            h: 30,
            d: 30
          },
          stock: [
            {
              name: 'Axolotl',
              amount: 1
            }
          ]
        },
        {
          id: 2,
          name: 'Fish Tank',
          img: 'https://placehold.it/200x100',
          size: {
            w: 100,
            h: 30,
            d: 30
          },
          stock: [
            {
              name: 'Male Fighter',
              amount: 1
            },
            {
              name: 'Neon Tetra',
              amount: 6
            }
          ]
        }
      ]
    }
  }
}
</script>
<style>
  .card {
    background-color: #474747;
    color: white;
    margin: 10px 20px;
  }
  .card .card-image {
    width: 100%;
  }
  .card .card-inner {
    padding: 6px 0;
  }
</style>


